   <div>第{{n}}手</div>
   <div class="row">
    <Cell @click="onClickCell(0, $event)" v-bind:n="n"/>
    <Cell @click="onClickCell(1, $event)" v-bind:n="n"/>
    <Cell @click="onClickCell(2, $event)" v-bind:n="n"/>
   </div>
   <div class="row">
    <Cell @click="onClickCell(3, $event)" v-bind:n="n"/>
    <Cell @click="onClickCell(4, $event)" v-bind:n="n"/>
    <Cell @click="onClickCell(5, $event)" v-bind:n="n"/>
   </div>
   <div class="row">
    <Cell @click="onClickCell(6, $event)" v-bind:n="n"/>
    <Cell @click="onClickCell(7, $event)" v-bind:n="n"/>
    <Cell @click="onClickCell(8, $event)" v-bind:n="n"/>
   </div>
     结果：{{result==null ?"胜负未分":`胜方为${result}`}}
   </div>
 
 </div>
