#对应js代码
<script>
import Cell from "./Cell";
export default{
  //组件化Cell 
  components:{Cell},
  data(){
    return{
     n:0,
     map:[
      [null,null,null],
      [null,null,null],
      [null,null,null]
     ],
     result:null
    };

  },
  methods:{
    onClickCell(i,text) {
      console.log(`${i}被点击,内容是：${text}`);
      this.map[Math.floor(i/3)][i%3]=text;
      this.n=this.n+1;
      this.tell();
    },
    //tell判断 输出结果
    tell(){
      const map=this.map

      for(let i=0;i<2;i++){
       if(map[i][0]!=null && map[i][0]==map[i][1]&&map[i][i]==map[i][2]){
         this.result=map[i][0];
       }
      }
      for(let j=0;j<2;j++){
        if(map[0][j]!=null&&map[0][j]==map[1][j]&&map[1][j]==map[2][j]){
          this.result=map[0][j];
        }
        if(map[0][0]!=null&&
        map[0][0]==map[1][1]&&
        map[1][1]==map[2][2]){
          this.result=map[0][0]
        }
        if(map[0][2]!=null&&
        map[0][2]==map[1][1]&&
        map[1][1]==map[2][0]){
          this.result=map[0][2]
        }
      }

    }
  }

}
</script>

