<template>
<div>
  <div class='cell'  v-on:click='onClickSelf'>
    <template v-if="a">{{text}}</template>
    <template v-else></template>
  </div>
</div>
</template>
cell
#对应js代码
<script>
export default{
    //展示n 的值 声明接受n
    props:["n"],
  data(){
    //ES6 语法 函数
    return  { a: false,text:"" };
  },

  methods:{
      //点击后的触发事件 通知app被点了 通知
      onClickSelf(){
          if(this.text!=""){
              return;
          }
          this.a=true;
          //点击时赋值
          this.text=this.n%2===0?"x":"o";
          //通知app点击内容
          this.$emit("click",this.text);

      }
  }
}
</script>

<style>
.cell{
  border:1px solid black;
  width:100px;
  height:100px;
  display:flex;
  justify-content:center;
  align-items: center;
  font-size:80px;
  
}
</style>
